import React, { Component } from 'react';
import {Glyphicon, PageHeader} from 'react-bootstrap';
import { connect } from 'react-redux';
import { sendMessage } from './chat';
import './App.css';
import Input from './react-speech-recognition-input';
import Bot from './images/bot.png';
import User from './images/user.png';
import Robo from './images/robo.png';
import UserHead from './images/userhead.png';
import { Gmaps, Marker } from 'react-gmaps';
import YouTube from 'react-youtube'
import Ticket from './ticket';
import VoicePlayer from '../src/react-voice-components/lib/VoicePlayer';
import Request from 'superagent';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { value: '' }
  }

  changingstate = (e) => {
    this.setState({ value: e.target.value })
  }
  keypress(e) {
    if (e.key === 'Enter') {
      e.preventDefault();
      this.setState({
        value: '',
      });
    }

  }
  reset() {
    this.setState({
      value: '',
    });

  }
  
  getmap = (lat, lang) => {
    console.log(lat, lang);
  }
  onMapCreated(map) {
    map.setOptions({
      disableDefaultUI: true
    });
  }
  scrollToBottom = () => {
    var elm = document.getElementById("ap");
    elm.scrollIntoView();
  }

  componentDidMount() {
    this.scrollToBottom();
  }

  componentDidUpdate() {
    this.scrollToBottom();
  }
  render() {
    window.setInterval(function() {
      var elem = document.getElementById('element');
      elem.scrollTop = elem.scrollHeight;
    }, 2000);
    var onMapCreated = function (map) {
      map.setOptions({
        disableDefaultUI: true
      });
    }
   // var _onReady = function (event) {
      // access to player in all event handlers via event.target
      //event.target.pauseVideo();
    //}
    const { feed, sendMessage } = this.props;
    const opts = {
      height: '200',
      width: '300',
      playerVars: {
        autoplay: 1
      }
    };
  
    return (
      <div id="ap" className="App"  >
        <div>
             <h2>BPOST DELIVERY CONFIRMATION </h2>
        </div>
        
        <div id="result">
        <div id="element">
          {feed.map(function (entry, a) {
            if (entry.sender === 'bot') {
              return (<div className="botPad" key={a}><br /><div className='stylebot'><VoicePlayer play text={entry.text}/>{entry.text}</div></div>)
            } else if (entry.sender === 'user') {
              return (<div className="userPad" key={a}><br /><div className='styleuser'>{entry.text}</div></div>)
            } 
            })}
          </div>
        </div>
        <div className="input_bar">
        <Input type='text' className="test" onChange={(value) => console.log(value)} onEnd={(value) => { console.log(value); sendMessage(value); }} />
        {/*<input type='text' value={this.state.value} onChange={this.changingstate} onKeyDown={(e) => e.keyCode === 13 ? sendMessage(e.target.value) : null} tabIndex='0' onKeyPress={this.keypress.bind(this)} />*/}
        </div>

      </div>
    );
  }
}
const mapStateToProps = state => ({
  feed: state
})
export default connect(mapStateToProps, { sendMessage })(App);


